.ViewProject {
  width: 100%;
  height: 80%;
  position: fixed;
}
@media screen and (orientation: portrait) {
  .ViewProject {
    width: 100vw;
    height: 100vh;
  }
}
.ViewProject__title {
  text-align: center;
  font-size: 6vh;
  padding-top: 4vh;
  padding-right: 4vh;
}
@media screen and (orientation: portrait) {
  .ViewProject__title {
    width: 100vw;
    padding-right: 0;
  }
}
.ViewProject__buttons {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 80%;
}
@media screen and (orientation: portrait) {
  .ViewProject__buttons {
    height: 60%;
  }
}

.viewButton {
  background-color: rgb(153, 1, 153);
  border: none;
  width: 40vh;
  height: 6vh;
  cursor: pointer;
  border-radius: 2vh;
  font-size: 2.25vh;
}/*# sourceMappingURL=ViewProject.css.map */